*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.header-nav{
    width: 100%;
    height: 80px;
    background: darkgrey;
    display: flex;
    justify-content: center;
    align-self: center;
}
.nav-list{
    list-style-type: none;
    display: flex;
    justify-content: center;
    align-items: center;
}
.nav-item{
    margin: 10px 15px;
    padding: 10px 25px;
    font-size: 16px;
    height: 40px;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    color: #fff;
    box-sizing: border-box;
    cursor: pointer;
}
.button-burger{
    display: none;
}
.button-active,
.button-active::before,
.button-active::after{
    width: 28px;
    height: 5px;
    background: #fff;
}
.button-active::before{
    content: '';
    position: absolute;
    bottom: 10px;
}
.button-active::after{
    content: '';
    position: absolute;
    top: 10px;
}
.close{
    transform: rotate(45deg);
    transition: transform 0.1s;
}
.close::before,
.close::after{
    top: 0;
    transform: rotate(90deg);
    transition: transform 0.2s;
}

@media (max-width: 768px){
    .header-nav{
        height: 80px;
    }
    .burger-open{
        flex-direction: column;
        width: 100%;
        height: max-content;
        background: darkgrey;
        list-style-type: none;
        transform: translateY(50px);
        transition: transform .3s;
    }
    .burger-close{
        list-style-type: none;
        transform: translateY(-250px);
    }
    .nav-item{
        margin: 10px 0px;
        padding: 0;
        font-size: 16px;
        height: 60px;
    }
    .button-burger{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 45px;
        height: 45px;
        position: absolute;
        top: 15px;
        right: 15px;
        z-index: 10;
        background: darkslategray;
        border-radius: 5px;
        overflow: hidden;
        cursor: pointer;
    }
}

.content-list{
    padding: 10px;
    margin: 0 10px;
}
.off{
    width: 100px;
    height: 50px;
    background: black;
    display: block;
}
.on{
    width: 100px;
    height: 50px;
    background: black;
    display: none;
}
.box{
    width: 100px;
    height: 50px;
    background: red;
    display: block;
}
